import ApiService from "@/core/services/ApiService";
import JwtService from "@/core/services/JwtService";
import axios from 'axios';
import {httpOptionsFile, httpOptions} from '../../global';

const apiServie = new ApiService();

export default class LotteryService{
  getData() {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/lottery`, httpOptions());
  }

  edit(id: any) {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/lottery/${id}`, httpOptions());
  }

  create(data: any) {
    return axios.post(`${ApiService.vueInstance.axios.defaults.baseURL}/lottery`, data, httpOptions());
  }

  update(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/lottery/${id}`, data, httpOptions());
  }

  updateWinner(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/lottery/winner/${id}`, data, httpOptions());
  }

  destroy(id:any) {
    return axios.delete(`${ApiService.vueInstance.axios.defaults.baseURL}/lottery/${id}`, httpOptions());
  }

  changeStatus(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/lottery/${id}/status`, data, httpOptions());
  }

  changeCompleted(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/lottery/${id}/completed`, data, httpOptions());
  }

  saveAnimation(id: any, data: any) {
    return axios.patch(`${ApiService.vueInstance.axios.defaults.baseURL}/lottery/${id}/animation`, data, httpOptions());
  }
}
