
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import LotteryService from "@/store/modules/lottery/service/lottery.service";
import ILottery from "@/store/modules/lottery/interface/lottery.interface";
import * as animationHelper from "@/store/helpers/animation.helper";
import * as validationHelper from "@/store/helpers/validation.helper";
import Swal from "sweetalert2/dist/sweetalert2.js";

export default defineComponent({
  name: "Lotteries",
  components: {
  },
  setup() {
    const router = useRouter();
		const lotteryService = new LotteryService();
		const lotteries = ref<Array<any>>([]);
		const lottery = ref<ILottery>({
			id: null,
			name: '',
			visible: false,
			duration: 0,
			position: '',
			completed: false,
			winners: [],
			site_id: null
		});

		const getData = ()=>{
			lotteries.value = []
			lotteryService.getData()
			.then((response) => {
				response.data.forEach((element: any) => {
					element.visible = (element.visible == 1)
					element.completed = (element.completed == 1)
				});
				lotteries.value = response.data
			})
			.catch((error) => {
				
			});
		}

		const saveStatus = (lottery: any) => lotteryService.changeStatus(lottery.id, lottery)
        .then((response) => {
            Swal.fire(
            'Confirmación!',
            'Información actualizada',
            'success'
            )
        })
        .catch((error) => {
            Swal.fire(
            'Oh no!',
            'No se guardaron los cambios',
            'error'
            )
        });

		const saveCompleted = (lottery: any) => lotteryService.changeCompleted(lottery.id, lottery)
        .then((response) => {
            Swal.fire(
            'Confirmación!',
            'Información actualizada',
            'success'
            )
        })
        .catch((error) => {
            Swal.fire(
            'Oh no!',
            'No se guardaron los cambios',
            'error'
            )
        });

		const changeValue = (e: any, lottery: any) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea cambiar el estado?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						saveStatus(lottery);
					}
					else{
						lotteries.value.forEach((element: any, index: any) => {
							if(element.id == lottery.id){
								lotteries.value[index].visible = !lottery.visible;
							}
						});
					}
			})
		}

		const changeCompleted = (e: any, lottery: any) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea finalizar el sorteo?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Finalizar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						saveCompleted(lottery);
					}
					else{
						lotteries.value.forEach((element: any, index: any) => {
							if(element.id == lottery.id){
								lotteries.value[index].completed = !lottery.completed;
							}
						});
					}
			})
		}

		const deleteData = (lotteryId) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea eliminar el registro seleccionado?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Eliminar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						lotteryService.destroy(lotteryId)
						.then((response) => {
							Swal.fire(
							'Confirmación!',
							'Información eliminada exitosamente',
							'success'
							).then(result => {
								getData()
							});
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se completo la operación',
							'error'
							);
						});
					}
			})
			
		}

		const editAnimation = (lotteryData: any) => {
			lottery.value = Object.assign({}, lotteryData);
		}

		const validateAnimation = () => {
			let disabled = false;
			if(!lottery.value.id || isNaN(lottery.value.duration)) disabled = true;
			return disabled;
		}

		const saveAnimation = () => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea cambiar los cambios?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Guardar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						lotteryService.saveAnimation(lottery.value.id, lottery.value)
						.then((response) => {
							Swal.fire(
							'Confirmación!',
							'Información actualizada',
							'success'
							)
							.then((result) => {
								getData()
								document.getElementById('animation-close-button')?.click()
							});
							
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se guardaron los cambios',
							'error'
							)
						});
					}
			})
		}
		
		onMounted(() => {
			getData()
		});

		return {
			lotteries,
			changeValue,
			deleteData,
			changeCompleted,
			animationHelper,
			validationHelper,
			editAnimation,
			validateAnimation,
			saveAnimation,
			lottery
		};
  },
});
